/* ApplyPlacement.css */
.apply-placement-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .apply-placement-modal {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    width: 80%;
    max-width: 600px;
    max-height: 90vh;
    overflow-y: auto;
  }
  
  .apply-placement-modal h2, .apply-placement-modal h3 {
    color: #333;
    margin-bottom: 15px;
  }
  
  .student-details, .resume-upload, .application-questions {
    margin-bottom: 20px;
  }
  
  .student-details p {
    margin: 5px 0;
  }
  
  .resume-upload input {
    margin-top: 10px;
  }
  
  .question {
    margin-bottom: 15px;
  }
  
  .question label {
    display: block;
    margin-bottom: 5px;
  }
  
  .question textarea {
    width: 100%;
    height: 100px;
    padding: 5px;
  }
  
  .form-actions {
    display: flex;
    justify-content: space-between;
  }
  
  .form-actions button {
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .form-actions button[type="submit"] {
    background-color: #4CAF50;
    color: white;
  }
  
  .form-actions button[type="button"] {
    background-color: #f44336;
    color: white;
  }